import React, { Component } from 'react';
import Loading from './Loading';
import axios from 'axios';
import '../styles.css'

export default class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }
  componentDidMount() {
    axios.get('https://api.github.com/users/sazbi/repos')
      .then(res => {
        setTimeout(() => {
          const items = res.data;
          this.setState({ items, isLoaded: true });
        }, 1000);
        
      });
  }

  render() {
    if (this.state.error) {
      return <div>Error: {this.state.error.message}</div>;
    } else if (!this.state.isLoaded) {
      return <Loading/>;
    } else {
      return (
        <div>
          <div className="pageHeader">
            <h1>Github Projects</h1>
          </div>
          <div className="container">
            <div className="row">
              {this.state.items.map(item =>
                <div key={item.id} className="col-md-4 col-sm-6 col-12 flip-card">
                  <div className="flip-card-inner">
                    <div className="flip-card-front">
                      <h4><b>{item.name}</b></h4>
                      <p>{item.description}</p>
                    </div>
                    <div className="flip-card-back">
                      <h4><b>{item.name}</b></h4>
                      <h5><b>Owner: {item.owner.login}</b></h5>
                      <a href={'https://github.com/' + item.full_name} rel="noopener noreferrer" target="_blank"><i className="fab fa-github"></i></a>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      );
    }
  }
}