import React, { Component } from 'react';
import Loading from './Loading';
import '../styles.css';

export default class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true
    }
  };
  componentDidMount() {
    setTimeout(() => this.setState({ isLoading: false }), 1000);
  }
  render() {
    const { isLoading } = this.state;

    if (isLoading) {  
      return (
        <Loading/>
      ) 
    }
    return (
      <div>
        <div className="pageHeader">
          <h1>
            About Me
          </h1>
        </div>
        
        <div className="container">
          <p className="quote">
            Your work is going to fill a large part of your life, and the only way to be truly satisfied is to do what you believe is great work. And the only way to do great work is to love what you do. If you haven't found it yet, keep looking. Don't settle. As with all matters of the heart, you'll know when you find it. <br/><br/>-Steve Jobs
          </p>
        </div>
        <hr className="sectionDivider" />
        <div className="container">
          <p className="text">
            The quote above by Steve Jobs offers a little insight into my past, present and future. I have bounced around over the past 10 years learning new skills and trades while also finding myself in the process. Building webscale projects has been something that I have been interested in since my middle school days. I would spend hours in front of my family's computer learning different HTML and CSS tricks. After graduating High School I let my desires take a backseat for personal reasons. It would take me nearly 9 years before I would realize I had given up on something that truely made me happy, and when I made this realization I dove back in head first and haven't looked back since. The thirst for knowledge I had in my early years resurged and was greeted by an endless field of opertunity to learn.
          </p>
          <p className="focus">
            Current Focus: <i className="fab fa-react"></i> ReactJS, <i className="fab fa-vuejs"></i> VueJS
          </p>
        </div>
      </div>
    );
  }
}